:root {
  --navy: #0a192f;
  --light-navy: #112240;
  --light-slate: #a8b2d1;
  --white: #e6f1ff;
  --green: #64ffda;
  --dim-green: rgba(100, 255, 218, 0.1);
  --box-shadow: 0 0 8px rgba(17, 34, 64, 0.3);
}

@font-face {
  font-family: 'JetBrains';
  font-display: swap;
  src: url('../assets/fonts/jetbrains.woff2');
  unicode-range: U+0020-007E;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  outline: 0;
  margin: 0;
  padding: 0;
  transition: 0.3s;
}

html {
  scroll-behavior: smooth;
}

body {
  width: 100%;
  height: 100%;
  background: var(--navy);
  display: flex;
  justify-content: center;
}

#root {
  width: 100%;
  height: 100%;
  max-width: 1600px !important;
  display: flex;
  flex-direction: column;
  position: absolute;
  background: var(--navy);
  font-family: 'JetBrains';
}

a {
  text-decoration: none;
}

header {
  display: flex;
  width: 100%;
  margin: 0 auto 24px;
  padding: 24px 80px;
  align-items: center;
  justify-content: space-between;
}

header a svg {
  width: 48px;
  height: 48px;
}

.menu {
  display: none;
}

header * {
  user-select: none;
}

header nav a {
  font-size: 14px;
  font-weight: 600;
  color: var(--white);
  margin-left: 24px;
}

header nav .active {
  color: var(--green);
}

header nav a:hover {
  color: var(--green);
}

.btn-small {
  padding: 8px 16px;
}

.btn-giant {
  padding: 10px 20px;
  margin-top: 24px;
}

.btn-small,
.btn-giant {
  font-size: 14px;
  font-weight: 600;
  color: var(--green);
  border: 2px solid var(--green);
  display: inline-block;
  user-select: none;
  cursor: pointer;
}

.btn-small:hover,
.btn-giant:hover {
  background: var(--dim-green);
}

.title {
  font-size: 32px;
  color: var(--white);
  margin-bottom: 20px;
}

footer * {
  user-select: none;
}

footer {
  font-size: 14px;
  color: var(--white);
  text-align: center;
  margin: auto auto 0;
  padding: 24px;
  width: 100%;
}

footer a {
  font-weight: 600;
  color: var(--green);
}

main {
  padding: 0 140px;
  width: 100%;
  margin: 0 auto;
  height: auto !important;
  position: relative;
}

h1,
h2,
h3 {
  font-weight: 600;
}

p {
  font-size: 14px;
  line-height: 1.5em;
  color: var(--light-slate);
}

.social-media a svg {
  width: 24px;
  height: 24px;
  display: inline-block;
  margin: 0 4px 8px;
  fill: var(--light-slate);
}

.social-media a:hover svg {
  fill: var(--green);
}

.not-found {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-top: 40px;
}

.not-found h1 {
  font-size: 76px;
  color: var(--green);
}

.not-found h2 {
  font-size: 28px;
  color: var(--white);
  margin-bottom: 8px;
}

.home {
  margin: 40px 0;
}

.home h2 {
  font-size: 60px;
  color: var(--white);
  margin-left: -3px;
}

.home h1 {
  font-size: 24px;
  color: var(--green);
}

.home h3 {
  font-size: 22px;
  color: var(--white);
}

.home p {
  max-width: 450px;
}

.contact {
  width: 50%;
  text-align: center;
  margin: 40px auto;
}

.contact h1 {
  font-size: 48px;
  font-weight: 600;
  color: var(--white);
  margin-bottom: 12px;
}

.featured-container {
  margin-bottom: 48px;
}

.featured-card,
.featured-card-reverse {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 28px;
}

.featured-card {
  flex-direction: row-reverse;
}

.featured-card-reverse {
  flex-direction: row;
}

.featured-image {
  aspect-ratio: calc(16 / 9);
  margin-right: -32px;
  width: calc(45% + 64px);
}

.featured-card-reverse .featured-image {
  margin-left: -32px;
  margin-right: 0;
}

.featured-image img {
  width: 100%;
  display: block;
  aspect-ratio: calc(16 / 9);
  background: linear-gradient(-45deg, var(--light-navy), var(--light-slate));
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  box-shadow: var(--box-shadow);
  border-radius: 2px;
  color: transparent;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.featured-body {
  width: 55%;
  background: var(--light-navy);
  box-shadow: var(--box-shadow);
  border-radius: 2px;
}

.featured-body {
  padding: 32px 32px 32px 64px;
}

.featured-body p {
  text-align: justify;
}

.featured-card-reverse .featured-body {
  padding: 32px 64px 32px 32px;
}

.featured-body h3 a {
  font-size: 24px;
  color: var(--white);
  margin-bottom: 16px;
  display: inline-block;
}

.featured-body h3 a:hover {
  color: var(--green);
}

.featured-body h2 {
  font-size: 16px;
  color: var(--green);
}

.featured-tech {
  margin: 16px 0 8px 0;
}

.featured-tech span {
  font-size: 12px;
  color: var(--light-slate);
  margin-right: 12px;
}

.featured-link a svg {
  width: 24px;
  height: 24px;
  fill: var(--light-slate);
  margin-right: 8px;
}

.featured-link a:hover svg {
  fill: var(--green);
}

.sub-title {
  font-family: 'JetBrains';
  font-size: 28px;
  text-align: center;
  color: var(--white);
  margin-bottom: 32px;
}

.folders {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  grid-column-gap: 16px;
}

.folder::before {
  content: '';
  width: 35%;
  height: 16px;
  background: var(--light-navy);
  display: block;
  top: -10px;
  position: absolute;
  border-top-left-radius: 8px;
  border-top-right-radius: 24px;
}

.folder {
  width: 100%;
  margin-top: 8px;
  margin-bottom: 32px;
  background: var(--light-navy);
  border-radius: 4px;
  position: relative;
}

.folder-body {
  padding: 24px 32px;
}

.folder-body h3 a {
  color: var(--white);
  margin-bottom: 12px;
  display: inline-block;
}

.folder-body h2 {
  color: var(--green);
  font-size: 14px;
}

.folder-body h3 a:hover {
  color: var(--green);
}

.folder-body p {
  margin-bottom: 12px;
  text-align: justify;
}

.folder-tech {
  margin-bottom: 12px;
}

.folder-tech span {
  font-size: 12px;
  color: var(--light-slate);
  margin-right: 12px;
}

.folder-link {
  text-align: right;
}

.folder-link a svg {
  display: inline-block;
  fill: var(--light-slate);
  margin-right: 8px;
  width: 24px;
  height: 24px;
}

.folder-link a:hover svg {
  fill: var(--green);
}

.more {
  text-align: center;
  margin: 16px 0 32px;
}

.about {
  display: flex;
  justify-content: space-between;
}

.about .description {
  width: 60%;
}

.about .photo {
  aspect-ratio: 1;
  width: 30%;
  position: relative;
}

.about .photo img {
  aspect-ratio: 1;
  width: 90%;
  color: transparent;
  background: var(--green);
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  transition: 0.3s;
}

.about .description p {
  margin-bottom: 4px;
}

.about .photo .box {
  aspect-ratio: 1;
  width: 90%;
  border: 3px solid var(--green);
  position: absolute;
  bottom: 0;
  right: 0;
  transition: 0.3s;
}

.about .photo:hover img {
  top: 2.5%;
  left: 2.5%;
}

.about .photo:hover .box {
  bottom: 2.5%;
  right: 2.5%;
}

.about ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  list-style-type: none;
  margin-top: 12px;
}

.about ul li::before {
  content: '';
  display: inline-block;
  width: 8px;
  height: 8px;
  background: var(--green);
  margin-right: 12px;
}

.about ul li {
  display: flex;
  align-items: center;
  font-size: 14px;
  color: var(--light-slate);
  margin-bottom: 6px;
}

@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.splash {
  width: 100%;
  height: 100%;
}

.splash svg {
  width: 120px;
  height: 120px;
  animation: fade 3s ease forwards;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  user-select: none;
}

@media (max-width: 992px) {
  header {
    padding: 24px 48px;
  }

  main {
    padding: 0 80px;
  }

  .title {
    text-align: center;
  }

  .home h2 {
    font-size: 52px;
    margin-left: -2.8px;
  }

  .home h1 {
    font-size: 20px;
  }

  .home h3 {
    font-size: 18px;
  }

  .contact {
    width: 80%;
  }
  .contact h1 {
    font-size: 42px;
  }

  .featured-body {
    width: 60%;
  }

  .featured-body {
    padding: 24px 24px 24px 48px;
  }

  .featured-card-reverse .featured-body {
    padding: 24px 48px 24px 24px;
  }

  .featured-image {
    width: calc(40% + 48px);
  }

  .featured-image {
    margin-right: -24px;
  }

  .featured-card-reverse .featured-image {
    margin-left: -24px;
  }

  .folders {
    grid-template-columns: repeat(2, 1fr);
  }

  .about {
    flex-direction: column-reverse;
    align-items: center;
    padding: 0 32px 32px;
  }
  .about .photo {
    width: 50%;
    margin-bottom: 24px;
  }
  .about .description {
    width: 95%;
  }
}

@media (max-width: 768px) {
  header {
    padding: 24px 42px;
  }

  .not-scrolled {
    overflow: hidden;
  }

  .blurry {
    filter: blur(6px) brightness(0.9);
    user-select: none;
  }

  .menu {
    display: block;
  }

  .menu svg {
    position: absolute;
    right: 42px;
    top: 28px;
  }

  .menu svg {
    width: 42px;
    height: 42px;
    fill: var(--green);
    cursor: pointer;
    z-index: 3;
  }

  .menu .close {
    opacity: 0;
  }

  .menu .open {
    opacity: 0;
  }

  .menu .active {
    opacity: 1;
    transition: 0.5s;
  }

  header nav {
    width: 300px;
    height: 100%;
    position: fixed;
    top: 0;
    right: -300px;
    background: var(--light-navy);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    z-index: 3;
    box-shadow: var(--box-shadow);
  }

  header .show {
    right: 0 !important;
  }

  main ~ header .show {
    filter: blur(8px);
  }

  header nav a {
    margin: 16px 0;
  }

  main {
    padding: 0 64px;
  }

  .title {
    font-size: 28px;
  }

  .home {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  .home h2 {
    font-size: 42px;
    margin-left: 0;
  }

  .home h1 {
    font-size: 18px;
  }

  .contact h1 {
    font-size: 42px;
  }

  .featured-card,
  .featured-card-reverse {
    flex-direction: column-reverse;
  }

  .featured-body,
  .featured-card-reverse .featured-body {
    width: 100%;
    padding: 64px 32px 32px;
  }

  .featured-image,
  .featured-card-reverse .featured-image {
    width: 85%;
    margin: 0 auto -48px;
  }

  .folders {
    grid-template-columns: 1fr;
  }

  .about {
    padding: 0 0 32px;
  }

  .about .photo {
    width: 55%;
  }
}

@media (max-width: 600px) {
  header {
    padding: 24px;
  }

  header nav {
    width: 100%;
    right: -100%;
  }

  header .menu svg {
    right: 24px;
  }

  main {
    padding: 0 24px;
  }

  .home h2 {
    font-size: 32px;
  }

  .home h1,
  .home h3 {
    font-size: 16px;
  }

  .home p {
    max-width: 95%;
  }

  .contact h1 {
    font-size: 36px;
  }

  .featured-body,
  .featured-card-reverse .featured-body {
    padding: 48px 24px 24px;
  }

  .featured-image,
  .featured-card-reverse .featured-image {
    margin-bottom: -32px;
  }

  .about .photo {
    width: 60%;
  }

  .about ul {
    grid-template-columns: 1fr;
  }
}
